html{
    margin:0;
    background: rgb(202, 238, 204);
    
}
#root{
    display: grid ;
    max-width:560px ;
    
    padding: 2rem 2rem;
    min-height:85vh;

}
.container {
    margin-top: 5vh ;
    padding: 1rem;
    background: rgb(202, 238, 204);
    border: teal solid 2px;
    gap: 1rem;
    display: grid;
    grid-template-columns:  1fr 1fr 1fr 1fr;
    grid-template-rows:  repeat(8,1fr) ;
    justify-content:center ;
    justify-self: center;
    min-width: 375px;
    min-height: 480px;
    height: 85vh;
    width: 90vw;
    
    touch-action: manipulation;
      
}
button {
    font-size: 2em;
    font-weight: bolder;
}
#display{
    border: solid 2px black;
    grid-column:1 / span 4;
    grid-row:  1 /span 3;
    background:white;
    font-size: 2em;
    overflow-wrap: break-word; 
    max-width: 90vw;    
    min-height: 5rem;
    min-width: 375;

}

    
#seven{

grid-column:1;
grid-row: 5;
}

#eight {
    grid-column:2;
    grid-row: 5;
}


#nine{
    grid-column:3;
    grid-row: 5;
}

#four{
    grid-column:1;
    grid-row: 6;
}
#five{
    grid-column:2;
    grid-row: 6;
}
#six{
    grid-column:3;
    grid-row: 6;
}
#one{
    grid-column:1;
    grid-row: 7;
}
#two{
    grid-column:2;
    grid-row: 7;
}
#three {
    grid-column:3;
    grid-row: 7;

}
#zero {
    grid-column: 1 / span 2;
    grid-row: 8;


}
#decimal{
    grid-column:3;
    grid-row: 8;

}

#equals{
    grid-column:4;
    grid-row: 8;

}
#add {
    grid-column:4;
    grid-row: 7;

}

#subtract{
    grid-column:4;

    grid-row: 6;

}
#multiply{
    grid-column:4;
    grid-row: 5;

}
#divide {
    grid-column:4;
    grid-row: 4;

}
#clear {
    grid-column:1 / span 2;
    grid-row: 4;
} 

#del{
    grid-column:3;
    grid-row:4;
}